
(function() {
  define('models/gadget_loader',['cdn.underscore', 'cdn.jquery', 'lib/config', 'models/settings'], function(_, $, cdn_config, settings) {
    var gadgetLoader;
    return gadgetLoader = {
      _cssLoaded: {},
      resolve: function(project) {
        var dfd;
        dfd = $.Deferred();
        if (!project) {
          return dfd.reject(new Error("Gadget project is required to resolve a gadget"));
        }
        $.when(this._fetchJs(project), this._linkCss(project)).done(function() {
          return dfd.resolve(project);
        }).fail(function(err) {
          return dfd.reject(err);
        });
        return dfd;
      },
      _fetchJs: function(project) {
        var dfd;
        dfd = $.Deferred();
        this._requirejs(project, function(ctor) {
          project.klass = ctor;
          return dfd.resolve();
        }, function(err) {
          return dfd.reject(err);
        });
        return dfd;
      },
      _linkCss: function(project) {
        var dfd;
        dfd = $.Deferred();
        if (this._cssLoaded[project.cssClassName()]) {
          return dfd.resolve;
        }
        $('<link rel="stylesheet" type="text/css" />').attr('href', project.css()).appendTo('head');
        this._waitUntilLoaded(project.css(), dfd.resolve);
        this._cssLoaded[project.cssClassName()] = true;
        return dfd;
      },
      _waitUntilLoaded: function(url, success) {
        return $('<img />').attr('src', url).on('error', success);
      },
      _requirejs: function(project, success, error) {
        var config, gadgetRequire;
        gadgetRequire = require;
        if (settings.get('sdk')) {
          config = _.extend({
            baseUrl: project.path()
          }, cdn_config);
          _.each(config.paths, function(v, k, obj) {
            return obj[k] = "/scripts/" + v;
          });
          gadgetRequire = require.config(config);
        }
        return gadgetRequire([project.main()], success, error);
      }
    };
  });

}).call(this);
